@import "./variables.scss";
@import "./responsive.scss";

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.fade-in-2 {
  opacity: 0; 
  animation: fadeInAnimation 2s ease-in forwards;
}

@keyframes fadeInAnimation {
  to {
      opacity: 1;
  }
}

.fade-in-4 {
  opacity: 0; 
  animation: fadeInAnimation 4s ease-in forwards;
}

@keyframes fadeInAnimation {
  to {
      opacity: 1;
  }
}

a {
  text-decoration: none;
  color: inherit;
}

.main {
  font-family: "Inter", sans-serif;
  background-color: $main-bg;
  color: $main-color;
  /* display: grid;
  grid-template-columns: 1fr 5fr; */
}

.container {
  display: flex;
  overflow-y: auto;
  min-height: 82vh;
}

.menuContainer {
  padding: 5px 20px;
  border-right: 2px solid $soft-bg;

  @include lg{
    max-width: max-content;
  }
}

.contentContainer {
  padding: 5px 20px;
  width: 85%;
  .pageContainer {
    .info{
      display: block;
      align-items: center;
      gap: 20px;

      button {
        display: block;
        align-items: center;
        padding: 0.25rem 0.5rem;
        border-radius: 10px;
        margin: -0.5rem 0 1rem 0;
  
        &:hover {
          background-color: $soft-bg;
          color: white;
          cursor: pointer;
        }
  
        .listItemTitle{
          @include lg{
            display: none;
          }
        }
      }
    }
  }
}

.error-message {
  padding: 0.5rem !important;
  margin: 1rem auto;
  text-align: center;
  border-radius: 5px;
  color: red;
  background-color: rgba($color: $soft-bg, $alpha: 0.2);
  border: 1px solid $soft-bg;
}
